
.about{
	overflow: hidden;
    background: url(../img/abbg.f2d1cdc8.png) no-repeat right top #fff;
    padding: 20px 40px;
	width: 100%;
	border-radius: 4px;
}
.avatar_img{
	position: relative;
    float: left;
    margin-right: 10px;
    background: -webkit-gradient(linear, left bottom, right top, from(rgba(0,153,255,.9)), to(rgba(42,228,197,.7)));
    background: linear-gradient(to top right, rgba(0,153,255,.9), rgba(42,228,197,.7));
    padding: 2px;
    width: 100px;
    border-radius: 50%;
}
.avatar_img img {
    width: 100%;
    border-radius: 50%;
}
.top_box {
    overflow: hidden;
    margin: 20px 0;
}
.top_box h3 {
    margin: 10px 0 10px;
    font-size: 18px;
}
.top_box p {
    color: #666;
    margin-bottom: 5px;
}
.gd_title {
    margin: 20px 0;
    font-size: 18px;
    background: url(data:image/png;base64,) no-repeat bottom right;
    width: 120px;
    clear: both;
    background-size: 40px;
    border-bottom: #000 1px dashed;
    padding-left: 10px;
    padding-bottom: 5px;
    display: inline-block;
}
.jianjie{
	margin-bottom: 15px;
    display: inline-block;
}
.avatar_img::before{
	position: absolute;
    content: "";
    background: url(data:image/png;base64,) no-repeat;
    bottom: 0px;
    right: 10px;
    height: 24px;
    width: 24px;
    background-size: 24px;
}


